import axios from "axios";

const getAll = axios.get('/api/persons')
    .then(response => response.data)

const addPerson = (newPerson) => axios.post('/api/persons', newPerson)
    .then(res => res.data)

const deletePerson = (id) => axios.delete(`/api/persons/${id}`)
    .then(res => res.data)

const changePerson = (id, newPerson) => axios.put(`/api/persons/${id}`, newPerson)
    .then(res => res.data)

const service = { getAll, addPerson, deletePerson, changePerson }

export default service
