import React, { useEffect, useState } from 'react'
import Persons from './components/Persons'
import Filter from './components/Filter'
import PersonForm from './components/PersonForm'
import servicePersons from './services/personsApis'
import Notification from './components/Notification'
import './index.css'

const App = () => {
  const [ persons, setPersons ] = useState([])
  const [ newName, setNewName ] = useState('')
  const [ newPhone, setNewPhone ] = useState('')
  const [ filterName, setFilterName ] = useState('')
  const [ message, setMessage] = useState('')
  const [ isError, setIsError] = useState(false)

  const fetchData = () => {
    servicePersons.getAll.then(response => {
      setPersons(response);
    })
  }
  useEffect(() => {
    fetchData();
  }, []);

  const handleNameChange = (event) => {
    setNewName(event.target.value)
  }
  const handleNumberChange = (event) => {
    setNewPhone(event.target.value)
  }

  const handleFilter = (event) => {
    setFilterName(event.target.value)
  }

  const handleMessage = (message, isErr) => {
    setMessage(message)
    isErr && setIsError(isErr)
    setTimeout(() => {
      setMessage(null)
      setIsError(false)
      }, 5000)
  }

  const handleDelete = (event, personToDelete) => {
    if (window.confirm(`Are you sure you want to delete ${personToDelete.name}`)) {
      servicePersons.deletePerson(personToDelete.id)
        .then(data => {
          setPersons(persons.filter(person => person.id !== personToDelete.id))
        });
    }
  }

  const handleNamesSubmit = (event) => {
    event.preventDefault();
    const newPerson = {
      name: newName,
      number: newPhone,
    }
    const existingPerson = persons.find(person => person.name === newName);
    if (existingPerson) {
      if (window.confirm(`${newName} is already added to phonebook. Replace the old number with new one?`)) {
        servicePersons.changePerson(existingPerson.id, newPerson)
          .then(res => {
            setPersons(persons.map(person => {
              if (person.name === newName) {
                person.number = newPhone;
              }
              return person;
            }))
            handleMessage(`${res.name} updated`);
          })
          .catch(err => handleMessage(`Information of ${newPerson.name} has allready been removed from the server.`, true))
      }
    } else {
      servicePersons.addPerson(newPerson)
        .then(res => {
          setPersons(persons.concat(res));
          handleMessage(`${res.name} added`);
        })
        .catch((err) => console.log(err));
      setNewName('');
      setNewPhone('');
    }
  }

  return (
    <div>
      <h2>Phonebook</h2>
      {message && <Notification message={message} isError={isError} />}
      <Filter filterName={filterName} handleFilter={handleFilter} />
      <PersonForm
        handleNamesSubmit={handleNamesSubmit}
        newName={newName}
        handleNameChange={handleNameChange}
        newPhone={newPhone}
        handleNumberChange={handleNumberChange}
      />
      <h3>Numbers</h3>
      <Persons persons={persons} filterName={filterName} handleDelete={handleDelete} />
    </div>
  )
}

export default App
