const Persons = ({ persons, filterName, handleDelete }) => persons.map(person => {
    if (filterName !== '') {
        return person.name.toLocaleLowerCase().includes(filterName.toLocaleLowerCase())
        ? (
            <div key={`${person.name}-${person.number}`}>
                <div key={person.name}>{person.name} {person.number}</div>
                <button>delete</button>
            </div>
        )
        : null
    }
    return (
        <div key={`${person.name}-${person.number}`}>
            <div style={{ display: 'inline' }} key={person.name}>{person.name} {person.number} </div>
            <button style={{ display: 'inline' }} onClick={(e) => handleDelete(e, person)}>
                delete
            </button>
        </div>
    )
})

export default Persons
